body {
  background: snow;
  margin: 0;
  font-family: 'Roboto Slab', serif; }
  body .container h1 {
    text-align: center;
    color: #613e3e; }
  body .container textarea {
    display: block;
    margin: 5vh auto 0;
    width: 100%;
    height: 30vh;
    border: none;
    border-bottom: 2px solid black;
    padding: 15px 20px;
    font-family: 'Roboto Slab', serif;
    font-size: 16px;
    font-weight: 600;
    text-align: justify;
    box-sizing: border-box;
    background: transparent;
    transition: all cubic-bezier(0.785, 0.135, 0.15, 0.86) 0.5s; }
    body .container textarea:focus, body .container textarea:hover {
      outline: none;
      background: #faeaea; }
    body .container textarea:focus {
      border-bottom: 5px solid black;
      height: 50vh; }
    body .container textarea:disabled {
      color: #979494;
      background: #ececec; }
  body .container .hint {
    float: right;
    margin: 5px;
    color: #613e3e;
    font-size: 12px; }
  body .container .random {
    margin: 5px;
    line-height: 2;
    cursor: pointer;
    color: #613e3e;
    background: none;
    border: none;
    font-family: 'Roboto Slab', serif;
    font-size: 16px;
    outline: none;
    position: relative; }
    body .container .random:disabled {
      color: #979494;
      pointer-events: none; }
    body .container .random span {
      color: #c91616;
      text-decoration: underline;
      font-weight: 800; }
    body .container .random .emoji {
      top: 25px;
      right: 30px;
      font-style: normal;
      text-decoration: none;
      font-size: 35px;
      position: absolute; }
  body .container button.cta {
    display: block;
    background: transparent;
    border: 5px solid #613e3e;
    color: #613e3e;
    margin: 15px auto;
    font-size: 22px;
    padding: 7px 15px;
    font-weight: bold;
    cursor: pointer;
    border-radius: none; }
    body .container button.cta:focus, body .container button.cta:hover {
      outline: none;
      background: #faeaea; }
    body .container button.cta:disabled {
      color: #979494;
      background: transparent;
      border-color: #979494;
      cursor: not-allowed; }
  body .container h1.REAL {
    font-size: 50px;
    color: green; }
  body .container h1.FAKE {
    font-size: 50px;
    color: darkred; }
  body .container .pipeline .show {
    text-align: center;
    text-decoration: underline;
    font-weight: 100;
    cursor: pointer; }
  body .container .pipeline .output {
    margin: 10px auto;
    padding: 30px;
    display: grid;
    grid-template-columns: 32% 32% 32%;
    grid-column-gap: 2%;
    -webkit-column-gap: 2%;
            column-gap: 2%;
    transition: all cubic-bezier(0.785, 0.135, 0.15, 0.86) 0.5s;
    height: auto;
    overflow: hidden;
    opacity: 1; }
    body .container .pipeline .output[hidden] {
      height: 0;
      opacity: 0; }
    body .container .pipeline .output div {
      text-align: justify; }
      body .container .pipeline .output div h2 {
        text-align: center; }

@-webkit-keyframes bounce {
  0%,
  20%,
  50%,
  80%,
  100% {
    -webkit-transform: translateY(0); }
  40% {
    -webkit-transform: translateY(-30px); }
  60% {
    -webkit-transform: translateY(-20px); } }

@keyframes bounce {
  0%,
  20%,
  50%,
  80%,
  100% {
    transform: translateY(0); }
  40% {
    transform: translateY(-12px); }
  60% {
    transform: translateY(-5px); } }

.bounce {
  -webkit-animation: bounce 1s infinite;
          animation: bounce 1s infinite; }

